/* Below polyfill is taken from https://github.com/angular/angular/issues/24769 as a solution
for IE 11 "Object doesn't support property or method 'matches'" problem */
if (!Element.prototype.matches) {
    Element.prototype.matches = Element.prototype.msMatchesSelector;
}

